import React from "react"
import "./Footer.scss"
import { Row, Col, Container } from "reactstrap"
import { FaMapMarker, FaPhone, FaEnvelope } from "react-icons/fa"
import logoE from "../../../assets/images/logo-e.jpg"
import logoK from "../../../assets/images/logo-k.jpg"
import logoS from "../../../assets/images/logo-s.jpg"

const Footer = () => {
  return (
    <footer>
      <Container>
        <Row className="p-0">
          <Col xs={6} xl={2}>
            <h5></h5>
            <ul>
              <li> </li>
              <li> </li>
              <li>  </li>
              <li>  </li>
              <li>   </li>
            </ul>
          </Col>
          <Col xs={6} xl={2}>
            <h5>  </h5>
            <ul>
              <li> </li>
              <li> </li>
              <li>   </li>
              <li> </li>
              <li> </li>
            </ul>
          </Col>
          <Col xs={12} sm={6} xl={4}>
            <h5></h5>
            <div>
              <FaMapMarker style={{ color: "green", marginLeft: "5px" }} />
              <span className="address">
                 -        48  8
              </span>
            </div>
            <div className="phone-number">
              <span>02x-xx243xx</span>
              <FaPhone />
            </div>
            <div className="email">
              <span>info@nodemarket.com</span>
              <FaEnvelope />
            </div>
          </Col>
          <Col xs={12} sm={6} xl={4}>
            <h5>  </h5>
            <div>
              <img src={logoE} width="33%" alt="" />
              <img src={logoK} width="33%" alt="" />
              <img src={logoS} width="33%" alt="" />
            </div>
          </Col>
        </Row>
      </Container>
      <div className="copywrite">
        &copy;      .
      </div>
    </footer>
  )
}

export default Footer